import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ACTIONS from '../../actions';
import File from './File';
import './ListFiles.css';
import Error from './Error';
import service from './service';
import FileNameUpdater from './FileNameUpdater';

class ListFiles extends Component {
    constructor(props) {
        super(props);
        this.state = {
            files: []
        }
    }

    render = () => {
        const files = this.state.files.map(
            (fileMetaData) => {
                let isSelected = this.state.selectedFile &&
                    fileMetaData.id === this.state.selectedFile.id;

                return <File key={fileMetaData.id}
                    {...fileMetaData}
                    onSelected={this.onFileSelected}
                    isSelected={ isSelected }/>
            });
        const fileNameUpdater = this.state.selectedFile ? 
            <FileNameUpdater {...this.state.selectedFile}/> : null;

        return (
            <div className="file-list" onScroll={this.onScroll}>
                <div className="file-list--pane-left">
                    <Error error={this.state.error || {}} />
                    <ol>{files}</ol>
                </div>
                <div className="file-list--pane-right">
                    {fileNameUpdater}
                </div>
            </div>)
    }

    onFileSelected = (file) => {
        this.setState((prevState) => {
            return Object.assign({}, prevState, { selectedFile: file });
        });
    }

    listFiles = (searchTerm) => {
        service.fetchFileList({searchTerm}).then(this.newFilesInResponse).catch(this.errorHandler);
    }

    nextPage = () => {
        service.fetchFileList({ pageToken: this.state.nextPageToken }).then(this.newFilesInResponse).catch(this.errorHandler);
    }

    errorHandler = (err) => { this.setState({ error: err.result.error }) }

    newFilesInResponse = (resp) => {
        const { store } = this.context;

        this.setState((prevState) => {
            let newState = Object.assign({}, prevState);
            newState.files = newState.files.concat(resp.result.files);
            newState.nextPageToken = resp.result.nextPageToken;
            newState.error = '';

            resp.result.files.forEach(f => store.dispatch(ACTIONS.setName(f)));

            return newState;
        });
    }

    componentWillReceiveProps = (newProps) => {
        this.setState({ files: [] });
        this.listFiles(newProps.searchTerm);
    }

    componentDidMount = () => {
        this.listFiles();
        window.addEventListener('scroll', this.onScroll);
    }

    onScroll = (ev) => {
        const ele = document.querySelector('.file-list');
        if (this.state.nextPageToken && !this.onGoingRequestPromise) {
            if (window.scrollY > (ele.scrollHeight - window.innerHeight)) {
                this.nextPage();
            }
        }
    }
}

ListFiles.contextTypes = {
    store: PropTypes.object
}

ListFiles.propTypes = {
    searchTerm: PropTypes.string.isRequired
}

export default ListFiles;



