import React, { Component } from 'react';
import PropTypes from 'prop-types';
import ACTIONS from '../../actions';
import './FileNameUpdater.css';
import service from './service';
import Tags from './tag/Tags';
import AddTag from './tag/AddTag';

const SEPARATORS = "[_.\\-,]";

class FileNameUpdater extends Component {

    constructor(props, context) {
        super(props);
        const fileName = context.store.getState().file.tags[props.id];
        this.store = context.store;
        this.state = {
            lastName: fileName,
            name: fileName
        }
    }

    componentWillReceiveProps = (props) => {
        const fileName = this.context.store.getState().file.tags[props.id];

        this.setState(prevState => ({
            lastName: fileName,
            name: fileName
        }));
    }

    componentDidMount = () => {
        const { store } = this.context;
        this.unsubscribeListener = store.subscribe(this.render);
    }

    componentWillUnmount = () => {
        this.unsubscribeListener();
    }

    render = () => {
        const fileName = this.getFileName();
        const saveClass = !this.isChanged() ? 'update--disabled' : 'update--enabled';
        const normalizeClass = !this.needNormalize() ? 'update--disabled' : 'update--enabled';

        return <div className="file-name-updater" id={this.props.id}>
            <dl>
                <dt><a href={this.props.webContentLink} alt="grab the content">{this.props.fullFileExtension}</a></dt>
                <dd>
                    <a href={this.props.webViewLink} target="_blank">
                        <img src={this.props.thumbnailLink} alt="thumbnail"></img><br />
                        <img src={this.props.iconLink} alt={this.props.description || 'icon'} />
                        {fileName}
                    </a>
                </dd>
                <dt>Tags</dt>
                <dd>
                    <AddTag onAdd={this.onAddTag} />
                    <Tags fileName={fileName} isRemoveable={true} onRemoveTag={this.onRemoveTag} />
                </dd>
                <dt>Actions</dt>
                <dd>
                    <button onClick={this.splitName} className={normalizeClass}>Normalize</button>
                    <button onClick={this.saveName} className={saveClass}>Save</button>
                </dd>
            </dl>
        </div>
    }

    onAddTag = (newTag) => {
        if (newTag === '' && this.isChanged()) {
            this.saveName();
        } else {
            this.setState((prevState) => {
                let newState = Object.assign({}, prevState);
                const tags = this.sanitizeFileName(prevState.name).split(' ');

                if (!tags.includes(newTag)) {
                    tags.push(newTag);
                    newState = {
                        lastName: prevState.name,
                        name: tags.join(' ')
                    }
                }
                return newState;
            });
        }
    }

    onRemoveTag = (tag) => {
        this.setState(prevState => {
            const tags = this.sanitizeFileName(prevState.name)
                .split(' ').filter(t => t !== tag).join(' ');

            return {
                lastName: prevState.name,
                name: tags
            }
        })
    }

    splitName = () => {
        const newName = this.sanitizeFileName(this.state.name);

        if (newName !== this.state.name) {
            this.setState((prevState) => ({
                lastName: prevState.name,
                name: newName
            }));
        }
    }

    saveName = () => {
        const { id } = this.props;
        const { name } = this.state;

        service.updateFileName(id, name).then((resp) => {
            const name = resp.result.name;
            this.store.dispatch(ACTIONS.setName({ id, name }));
            this.setState(prevState => ({
                lastName: name,
                name: name
            }));
        }).catch(e=>{
            this.setState(prevState => ({
                lastName: this.props.name,
                name: this.props.name
            }));
        });
    }

    sanitizeFileName = (name) => {
        return name.replace(new RegExp(SEPARATORS, 'g'), ' ').toLowerCase();
    }

    needNormalize = () => {
        return this.state.name.match(SEPARATORS);
    }

    getFileName = () => {
        return (this.isChanged() ? this.state.name :
            this.store.getState().file.tags[this.props.id]);
    }

    isChanged = () => {
        return this.state.lastName !== this.state.name;
    }
}

FileNameUpdater.contextTypes = {
    store: PropTypes.object
};

export default FileNameUpdater;


