import React, { Component } from 'react';
import './About.css';

export default class About extends Component {

    constructor(props){
        super(props);
        this.state={
            user: { 
                displayName: '', 
                emailAddress: '', 
                photoLink: ''
            },
            storageQuota: null
        }
    }

    componentDidMount() {
        fetchAbout().then((resp)=>{
            this.setState(resp.result);
        }).catch(()=>{debugger;});
    }

    render() {
        let image = '';
        if(this.state.user.photoLink) {
            image = <img className="about__profile-image--rounded"
                src={this.state.user.photoLink} alt="user"/>;
        }
        return <div className="about">
            <div className="about__profile-image">{image}</div>
            <div className="about__display-name">{this.state.user.displayName}</div>
            <div className="about__email-address">{this.state.user.emailAddress}</div>
        </div>
    }
}


function fetchAbout(params) {
    const queryParams = Object.assign({
        fields: `folderColorPalette,storageQuota,user`
    }, params || {});

    return window.gapi.client.drive.about.get(queryParams);
}


