import React, { Component } from 'react';

const KEY = 'apiKey';

/** 
 * Token authentication higher order component.
 * - It requests a valid token to enter in an input field
 * - Exports an invalidTokenCb to call for the wrapped component
 * - It can parse token from query string as Well. Example: "?apiKey=<token>"
 */
export default function authenticated(WrappedComponent) {
    return class extends Component {
        constructor(props) {
            super(props);
            let accessToken = localStorage.getItem(KEY) ||
                new URLSearchParams(document.location.search).get(KEY);
            this.state = {
                accessToken: accessToken,
                isAuthenticated: !!accessToken
            };
        }

        onChange = (event) => {
            localStorage.setItem(KEY, event.target.value);
            this.setState({
                accessToken: event.target.value,
                isAuthenticated: !!event.target.value
            })
        }

        invalidTokenCb = () => {
            this.setState({
                isAuthenticated: false
            });
        }

        render() {
            if (this.state.isAuthenticated) {
                return <WrappedComponent
                    token={this.state.accessToken}
                    invalidTokenCb={this.invalidTokenCb}
                    {...this.props} />;
            }
            return (
                <div>Unauthorized user, please enter a valid <a href="mailto:ypetya@gmail.com">API key</a> to continue!
                <input onChange={this.onChange} value={this.state.accessToken} />
                </div>)
        }
    }
}


