import React, { Component } from 'react';
import "./login.css";

const clientId = '895519360232-n8lmniufrn8o58v6qija7ut4eakp5r8t.apps.googleusercontent.com';
const discoveryDocs = [
    "https://people.googleapis.com/$discovery/rest?version=v1",
    "https://www.googleapis.com/discovery/v1/apis/drive/v3/rest"
];
const scopes = 'profile https://www.googleapis.com/auth/drive';

export default class GoogleOauth extends Component {

    constructor(props) {
        super(props);
        this.apiKey = this.props.token;
        this.state = {
            isSignedIn: false,
            loaded: false
        }
    }

    onLoad = () => {
        window.gapi.load('client:auth2', this.initClient);
    }

    initClient = () => {
        window.gapi.client.init({
            apiKey: this.apiKey,
            discoveryDocs: discoveryDocs,
            clientId: clientId,
            scope: scopes
        }).then(() => {
            // Listen for sign-in state changes.
            window.gapi.auth2.getAuthInstance().isSignedIn.listen(this.updateSigninStatus);
            // Handle the initial sign-in state.
            this.updateSigninStatus(window.gapi.auth2.getAuthInstance().isSignedIn.get());
        }).catch((err) => {
            this.props.invalidTokenCb(err.result);
        });
    }

    updateSigninStatus = (isSignedIn) => {
        this.setState((prevState) => {
            let userName = { userName: isSignedIn && prevState.userName };
            return Object.assign({}, prevState, {
                isSignedIn: isSignedIn,
                loaded: true
            },
                userName);
        });

        if (isSignedIn && !this.state.userName) {
            window.gapi.client.people.people.get({
                'resourceName': 'people/me',
                'requestMask.includeField': 'person.names'
            }).then((resp) => {
                this.setState((prevState) => {
                    return Object.assign({}, prevState, { userName: resp.result.names[0].givenName });
                });
            });
        }
    }

    signInOut = (isSignedIn) => {
        if (isSignedIn) {
            window.gapi.auth2.getAuthInstance().signIn();
        } else {
            window.gapi.auth2.getAuthInstance().signOut();
        }
    }

    render() {
        let signInOutButton = '';
        if (this.state.loaded) {
            signInOutButton = <button className="login__button" onClick={this.signInOut.bind(this, true)} >Sign in</button>;

            if (this.state.isSignedIn) {
                signInOutButton = <button className="login__button" onClick={this.signInOut.bind(this, false)}>Sign out</button>;
            }
        }
        let loginInfo = <div className="login__box">{signInOutButton}</div>;
        if (this.state.isSignedIn && this.state.userName) {
            loginInfo = <div className="login__box">{this.state.userName} {signInOutButton}</div>;
        }

        let facade = '';
        if (!this.state.loaded) {
            facade = <div><i className="icon-spin3" />Loading...</div>;
        } else if (!this.state.isSignedIn) {
            facade = 'Please login!';
        } else {
            facade = this.props.children;
        }

        return <div className="login">
            {loginInfo}
            {facade}
        </div>;
    }

    componentDidMount = () => {
        const script = document.createElement("script");

        script.src = "https://apis.google.com/js/api.js";
        script.onload = this.onLoad;
        script.async = true;

        document.body.appendChild(script);
    }
}


