import React, { Component } from 'react';
import './Search.css';

export default class Search extends Component {

    constructor(props) {
        super(props);
        this.state = { searchCriteria: '' };
    }

    onSearch = (ev) => {
        const searchCriteria = ev.target.value && ev.target.value.toLowerCase();
        if (searchCriteria && searchCriteria.length >= 3) {
            let matchingGistIds = this.props.getMatches(searchCriteria);
            if (typeof this.props.filter === 'function') {
                this.props.filter(matchingGistIds);
            }
        } else {
            this.onClear();
        }
        this.setState({ searchCriteria });
    }

    onClear = (ev) => {
        this.setState({ searchCriteria: '' });
        return this.props.clear();
    }

    render() {
        return (<span>
            <input className="search" type="text" onChange={this.onSearch} value={this.state.searchCriteria} />
        </span>);
    }
}


