import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './AddTag.css';

class AddTag extends Component {

    render() {
        return <div className="addTag"><input type="text" onKeyUp={this.onKeyUp}/></div>
    }

    componentDidMount = () => {
        this.input = document.querySelector('.addTag input');
    }

    onKeyUp = (key) => {
        if(key.key === 'Enter') {
            this.props.onAdd(this.input.value);
            this.input.value='';
        }
        return true;
    }
}

AddTag.propTypes = {
    onAdd: PropTypes.func.isRequired
}

export default AddTag;


