import React, {Component} from 'react';
import PropTypes from 'prop-types';
import './Tags.css';

class Tags extends Component {
    constructor(props) {
        super(props);
        this.tags=[];
        this.createTags(props);
    }

    render() {
        let tags = [];
        for(let tag of this.tags) {
            const removeButton = this.props.isRemoveable && 
                <button onClick={this.onRemove} tag={tag}>X</button>;
            tags.push(
            <span className="tag" key={`tag_${tags.length}`}>
                {tag}
                {removeButton}
            </span>);
        }
        return <div className="tags">{tags}</div>
    }

    onRemove = (event) => {
        const tag = event.currentTarget.getAttribute('tag');
        this.props.onRemoveTag(tag);
    }

    createTags = (props) => {
        if (props.fileName) {
            this.tags = [];
            this.newTags = props.fileName.split(/[\s-]/);

            for (let tag of this.newTags) {
                if (!this.tags.includes(tag)) {
                    this.tags.push(tag);
                }
            }
        }
    }

    componentWillReceiveProps = (newProps) => {
        this.createTags(newProps);
    }
}

Tags.propTypes = {
    isRemoveable: PropTypes.bool.isRequired,
    onRemoveTag: PropTypes.func,
    fileName: PropTypes.string.isRequired
}

export default Tags;


