
/**
 * Implemented throttling
 */
class Service {

    constructor() {
        this.onGoingRequestPromise = null;
    }

    fetchFileList = async (params) => {

        const queryParams = Object.assign({
            pageSize: 100,
            fields: `nextPageToken,files(\
                contentHints/thumbnail,fullFileExtension,\
                iconLink,id,kind,mimeType,modifiedTime,name,originalFilename,shared,\
                size,spaces,starred,thumbnailLink,viewedByMe,webContentLink,webViewLink,\
                owners(emailAddress,displayName,photoLink))`,
            orderBy: 'modifiedTime desc'
        },
            { q: this.constructCriteria(params && params.searchTerm) });

        if (params && params.pageToken) {
            queryParams.pageToken = params.pageToken;
        }

        return this.seq(() => {
            return window.gapi.client.drive.files.list(queryParams);
        });
    };

    updateFileName = async (id, newName) => {
        return this.seq(() => {
            const queryParams = {
                fileId: id,
                name: newName
            };

            return window.gapi.client.drive.files.update(queryParams);
        });
    }

    seq = async (cb, params) => {
        if (this.onGoingRequestPromise && !this.onGoingRequestPromise.done) {
            await this.onGoingRequestPromise;
            // eslint-disable-next-line no-throw-literal
            throw { result: { error: 'Forced to stop! :)' } };
        }

        this.onGoingRequestPromise = cb();

        return this.onGoingRequestPromise.then((resp) => {
            this.onGoingRequestPromise.done = true;
            return resp;
        })
    }


    /**
     * null -> all File
     * 'name = "photo-documentation"' -> exact match
     * 'mimeType="application/vnd.google-apps.folder"' -> folders
     */
    constructCriteria = (searchTerm) => {
        const intersectionCriteria = [
            'trashed = false',
            "mimeType != 'application/vnd.google-apps.folder'"
        ];

        if (searchTerm) {
            intersectionCriteria.push(`name contains "${searchTerm}"`);
        }
        return intersectionCriteria.join(' and ');
    }
}

export default new Service();


