import React, { Component } from 'react';
import './App.css';
import '../font';
import tokenAuth from './tokenAuth';
import GoogleOauth from './GoogleOauth';
import About from './drive/About';
import SearchFiles from './SearchFiles';

class App extends Component {

  componentWillMount = () => {
    this.GoogleOauthWithApiKey = tokenAuth(GoogleOauth);
  }

  render() {
    const GoogleOauthWithApiKey = this.GoogleOauthWithApiKey;
    return (
      <div className="App">
        <GoogleOauthWithApiKey>
          <header className="App-header">
            <About />
          </header>
          <SearchFiles />
        </GoogleOauthWithApiKey>
      </div>
    );
  }

}

export default App;



