import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './File.css';
import Tags from './tag/Tags';

class File extends Component {

    constructor(props, context) {
        super(props);

        this.state = { fileName: context.store.getState().file.tags[this.props.id] };
    }

    componentDidMount = () => {
        const { store } = this.context;

        this.unsubscribeListener = store.subscribe(this.rerender);
    }

    componentWillUnmount = () => {
        this.unsubscribeListener();
    }

    render = () => {
        const { fileName } = this.state;

        let owners = this.props.owners.map(owner =>
            <img key={owner.emailAddress} className="owner__picture" src={owner.photoLink} alt={owner.displayName} />);

        return <li className={this.getClassName()} onMouseEnter={this.onShowFile}>
            {this.modifiers()}
            {owners}
            <a href={this.props.webViewLink} target="_blank"><img src={this.props.iconLink} alt={this.props.description || 'icon'} /></a>
            <Tags fileName={fileName} isRemoveable={false} />
        </li>;
    }

    getClassName = () => {
        const classes = ['file'];
        if (this.props.isSelected) {
            classes.push('selected');
        }
        return classes.join(' ');
    }

    rerender = () => {
        const { store } = this.context;
        const fileName = store.getState().file.tags[this.props.id];

        this.setState(prevState => ({ fileName }));
    }

    onShowFile = () => {
        this.props.onSelected(this.props);
    }

    modifiers() {
        return <span className="file__modifiers">
            {this.props.shared ? <i className="icon-users" /> : ''}
            {this.props.starred ? <i className="icon-star" /> : <i className="icon-star-empty" />}
            {this.props.spaces.includes('photos') ? <i className="icon-camera" /> : ''}
        </span>;
    }
}

File.contextTypes = {
    store: PropTypes.object
};

File.propTypes = {
    id: PropTypes.string.isRequired,
    webViewLink: PropTypes.string.isRequired,
    iconLink: PropTypes.string.isRequired,
    shared: PropTypes.bool.isRequired,
    starred: PropTypes.bool.isRequired,
    spaces: PropTypes.array.isRequired
}

export default File;


