
export default (state = { tags: {} }, action = { file: {} }) => {
    const newState = Object.assign({}, state);
    if (action.file) {
        const { id } = action.file;
        switch (action.type) {
            case 'SELECT':
                newState.selected = id;
                break;
            case 'SET_NAME':
                const { name } = action.file;
                newState.tags = Object.assign({}, newState.tags, {
                    [id]: name
                });
                break;
            default:
        }
    }

    return newState;
};


